import React, { Component } from 'react';

import CalcScreen from './CalcScreen';
import CalcKey from './CalcKey';

function tackRight(cur, num) {
  return cur === null
    ? parseFloat(num)
    : parseFloat([...cur.toString(), num].join(''));
}

const keys = [
  {
    value: 'AC',
    area: 'ac',
    fun: (prev, cur) => {
      return { prev: 0, cur: 0, neg: false };
    },
  },
  {
    value: '+/-',
    area: 'pm',
    fun: (prev, cur) => {
      return { prev, cur: -cur };
    },
  },
  {
    value: 'Del',
    area: 'del',
    fun: (prev, cur) => {
      const res = +cur.toString().slice(0, -1);
      return { prev, cur: res };
    },
  },
  {
    value: '÷',
    area: 'div',
    fun: (prev, cur) => {
      const res = prev / cur;
      return { prev, cur: res };
    },
  },
  {
    value: '7',
    area: 'sev',
    fun: (prev, cur) => {
      const res = tackRight(cur, '7');
      return { prev, cur: res };
    },
  },
  {
    value: '8',
    area: 'eig',
    fun: (prev, cur) => {
      const res = tackRight(cur, '8');
      return { prev, cur: res };
    },
  },
  {
    value: '9',
    area: 'nin',
    fun: (prev, cur) => {
      const res = tackRight(cur, '9');
      return { prev, cur: res };
    },
  },
  {
    value: '×',
    area: 'mul',
    fun: (prev, cur) => {
      return { prev: 0, cur: 0 };
    },
  },
  {
    value: '4',
    area: 'fou',
    fun: (prev, cur) => {
      const res = tackRight(cur, '4');
      return { prev, cur: res };
    },
  },
  {
    value: '5',
    area: 'fiv',
    fun: (prev, cur) => {
      const res = tackRight(cur, '5');
      return { prev, cur: res };
    },
  },
  {
    value: '6',
    area: 'six',
    fun: (prev, cur) => {
      const res = tackRight(cur, '6');
      return { prev, cur: res };
    },
  },
  {
    value: '−',
    area: 'sub',
    fun: (prev, cur, neg) => {
      const res = tackRight(cur, '');
      return neg
        ? { prev: prev - cur, cur: null, neg: true }
        : { prev: prev + cur, cur: null, neg: true };
    },
  },
  {
    value: '1',
    area: 'one',
    fun: (prev, cur) => {
      const res = tackRight(cur, '1');
      return { prev, cur: res };
    },
  },
  {
    value: '2',
    area: 'two',
    fun: (prev, cur) => {
      const res = tackRight(cur, '2');
      return { prev, cur: res };
    },
  },
  {
    value: '3',
    area: 'thr',
    fun: (prev, cur) => {
      const res = tackRight(cur, '3');
      return { prev, cur: res };
    },
  },
  {
    value: '+',
    area: 'add',
    fun: (prev, cur, neg) => {
      return neg
        ? { prev: prev - cur, cur: null, neg: false }
        : { prev: prev + cur, cur: null, neg: false };
    },
  },
  {
    value: '0',
    area: 'zer',
    fun: (prev, cur) => {
      const res = tackRight(cur, '0');
      return { prev, cur: res };
    },
  },
  {
    value: '.',
    area: 'dec',
    fun: (prev, cur) => {
      const res = tackRight(cur, '.');
      return { prev, cur: res };
    },
  },
  {
    value: '=',
    area: 'eq',
    fun: (prev, cur, neg) => {
      return neg
        ? { prev: cur, cur: prev - cur }
        : { prev: cur, cur: prev + cur };
    },
  },
];

class Calculator extends Component {
  state = {
    prev: 0,
    cur: 0,
    neg: false,
  };

  onClick = fun => () =>
    this.setState(({ prev, cur, neg }) => {
      return fun(prev, cur, neg);
    });

  render() {
    const { prev, cur } = this.state;
    const display = cur === null ? parseFloat(prev) : parseFloat(cur);
    return (
      <div className="Calc">
        <div className="CalcModel">Reactulator</div>
        <div className="CalcGrid">
          <React.Fragment>
            <CalcScreen>{display}</CalcScreen>
            {keys.map(o => (
              <CalcKey
                key={o.value}
                area={o.area}
                onClick={this.onClick(o.fun)}
              >
                {o.value}
              </CalcKey>
            ))}
          </React.Fragment>
        </div>
      </div>
    );
  }
}

export default Calculator;
